<template>
  <div id="messageSender">
    <div class="toolbar">
      <el-popover
        placement="top-start"
        width="560"
        trigger="click">
        <emotion @emotion="handleEmotion" :height="200" ></emotion>
        <i class="iconfont icon-biaoqing" slot="reference"></i>
      </el-popover>

      <el-upload
        class="avatar-uploader"
        action="upload"
        :beforeUpload = "sendFileMessage"
        :show-file-list="false">
        <i class="iconfont icon-iconset0196"></i>
      </el-upload>
    </div>
    <MessageInput class="message-input" ref="select_frame" :content="message" @send="sendTextMessage" placeholder='Ctrl+Enter'></MessageInput>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
import Emotion from './Emotion/index'
import MessageInput from './MessageInput'

export default {
  name: 'MessageSend',

  data () {
    return {
      sessionId: null,
      message: {txt: ''},
      fileList: []
    }
  },

  components: {
    Emotion,
    MessageInput
  },

  computed: {
    ...mapGetters({session: 'chatroom/api_get_chatroom', user: 'account/api_current_account'})
  },

  height: {
    type: Number,
    default: 200
  },

  methods: {
    handleEmotion (i) {
      this.message.txt += this.emotion(i)
    },

    /**
     * 
     *
     *
     * */
    emotion (res) {
      let word = res.replace(/#|;/gi, '')
      const list = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', 'NO', 'OK', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
      let index = list.indexOf(word)
      return `<img src="https://res.wx.qq.com/mpres/htmledition/images/icon/emotion/${index}.gif" align="middle">`
    },

    /**
     * 
     *
     * */
    sendFileMessage (params) {
      this.$store.dispatch('chatroom/api_upload_file', params).then((data) => {
        this.$store.dispatch('chatroom/api_send_file_message', {
          sessionId: this.session.id,
          message: data,
          user: this.user
        }).then(res => {
          if (res) {
            this.message.txt = ''
          }
        })
      })

      return false
    },

    sendTextMessage () {
      this.$store.dispatch('chatroom/api_send_text_message', {
        sessionId: this.session.id,
        message: this.message.txt,
        user: this.user
      }).then(res => {
        if (res) {
          this.message.txt = ''
        }
      })
    }
  },

  mounted () {
    // let sessionId = this.session.id
    // this.$refs.select_frame.onpaste = (e) => {
    //   console.log(e.dataTransfer)
    //   let clipboardData = e.clipboardData
    //   if (clipboardData) {
    //     let items = clipboardData.items
    //     if (items && items.length) {
    //       let item = null
    //       for (var i = 0; i < clipboardData.types.length; i++) {
    //         if (clipboardData.types[i] === 'Files') {
    //           item = items[i]
    //           if (item && item.kind === 'file') {
    //             console.log('sending file')
    //             this.$store.dispatch('chatroom/api_upload_file', {
    //               sessionId: sessionId,
    //               file: item.getAsFile()
    //             }).then(res => {
    //               if (res) {
    //                 // this.message = ''
    //               }
    //             })
    //           }
    //         }
    //       }
    //     }
    //   }
    // }
    //
    // this.$refs.select_frame.ondragleave = (e) => {
    //   // 
