<template>
  <div class="emotion" >
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'emotion',
  mounted () {
    const name = this.$el.innerHTML
    const list = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', 'NO', 'OK', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
    let index = list.indexOf(name)
    let imgHTML = `<img src="https://res.wx.qq.com/mpres/htmledition/images/icon/emotion/${index}.gif">`
    this.$nextTick(() => {
      this.$el.innerHTML = imgHTML
    })
  }
}
</script>
<style scoped>
  .emotion {
    display: inline-block;
    margin: 10px;
  }
  .ly-static-emotion {
    width: 24px;
    height: 24px;
    display: inline-block;
  }
</style>



